<template>
  <div>

    <div class="all_list"
         style="padding:15px"
         v-for="(item,index) in arrReview"
         :key="index">
      <van-image round
                 width="50px"
                 height="50px"
                 :src="item.avatar" />
      <div class="all-right">
        <p><label>{{item.username}}</label> <span>{{item.rateTime}}</span></p>
        <p>
          <van-rate v-model="value"
                    allow-half
                    void-icon="star"
                    void-color="#eee"
                    :size="15"
                    color="#FFC300" />
          <span>{{item.deliveryTime}}分钟内送达</span>
        </p>
        <p>
          {{item.text}}
        </p>
        <van-tag color="#7232dd"
                 plain
                 v-for="(rec,index) in item.recommend"
                 :key="index"
                 style="margin-left:5px">{{rec}}</van-tag>
      </div>
    </div>

  </div>
</template>

<script>
import { shop_ratings } from '@/api/api'
import { getchtime } from '../../utils/rateTime'
export default {
  created () {
    //取得评价
    shop_ratings().then(res => {
      for (const obj of res.data.data) {
        obj.rateTime = getchtime(obj.rateTime)
        console.table(obj.rateType)
      }
      this.arrReview = res.data.data
    })
  },
  data () {
    return {
      value: 4,
      arrReview: [],
    }
  }
}
</script>

<style lang="less" scoped>
.all_list {
  display: flex;
  justify-content: flex-start;
  .all-right {
    margin-left: 15px;
    flex: 1;

    p:first-child {
      justify-content: space-between;
    }
    p {
      display: flex;
      align-items: center;
      font-size: 12px;
      line-height: 25px;
      span {
        color: #909090;
      }
    }
  }
}
</style>